 components: {
   RainbowMenu,
   SearchButtonGrid,
   MapVue,
   MapSearchComponent,
   LanguageDropdown,
 },

 data() {
   return {
    selectedLanguage: 'ko', // 초기 선택 언어 설정
     carItems: [
       { text: 'SmileNumber Mobile 1' },
       { text: 'SmileNumber Mobile 2' },
