       { text: 'SmileNumber Mobile 21' }
     ],
     search: '',
     items: [
       { imgSrc: require('@/assets/1.png'), url: '/CarSpace1', altText: 'Image 1' },
       { imgSrc: require('@/assets/2.png'), url: '/CarSpace2', altText: 'Image 2' },
