     ],
     secondItems: [
      
       
       // 필요한 만큼 이미지 항목을 추가하세요.
     ],
     pageTexts: [
        
     ]
   };
 },
 computed: {
   searchResults() {
     if (!this.search) return [];
     return this.pageTexts.filter(text => text.toLowerCase().includes(this.search.toLowerCase()));
   }
 },
 methods: {
  changeLanguage(lang) {
        this.$i18n.locale = lang;
      },
   getImageUrl(index) {
     return require(`@/assets/HyundaiCar${index}.png`);
   },
   goToHyundaiCar(index) {
     this.$router.push({ name: `HyundaiCar${index}` });
   },
   enlargeImage(index) {
     const image = document.querySelector(`.image-${index + 1}`);
     image.classList.add('enlarged');
   },
   resetImage(index) {
     const image = document.querySelector(`.image-${index + 1}`);
     image.classList.remove('enlarged');
   },
   handleSearch() {
     const searchResult = this.searchResults;

     if (searchResult.length > 0) {
       // 검색 결과 중 첫 번째 항목으로 이동
       this.$router.push('/' + searchResult[0]);
     } else {
       // 검색 결과가 없는 경우 사용자에게 메시지 표시 등의 처리
       console.log("No results found.");
     }
   },

 }
};
</script>

