        <p>2. Kupanga Kudandaula: Pempho za dokomente zomwe zimapereka monga gawo la gawo lozolowera, mabuku a mabuku a galimoto, pampamatu ya pulogalamu, ndi mabuku a usafiri.</p>
        <p>3. Kupanga Usafiri: Kukhala ndi kumanga ndi kompani ya usafiri ndi kumanga magalimoto ku kapolo.</p>
        <p>4. Kulangiza mabukhu: Kulembetsa mabukhu a mabukhu pa kapolo yakale.</p>
    </section>
    <br><br>

    <section>
        <h2>	Mabwino Othandiza</h2>

        <!-- Sankhani <strong> tags to <p> tags ndi kusintha malipiro a tsiku lomwe. -->
        <p>1. Chilango cha Galimoto: Ndalama ya galimoto yomwe yagulidwa.</p>
        <p>2. Malipiro a Usafiri: Malipiro a usafiri wa maloya ndi makasitomala a pulogalamu.</p>
        <p>3. Malipiro a Nsurance: Malipiro a nsurance ya maloya.</p>
        <p>4. Kulamulira ndi Malipiro: Chilungamo ndi malipiro a maloya mu dziko lalikulu.</p>
    </section>
    <br><br>

    <section>
        <h2>	Mchitidwe wa Usafiri</h2>

        <!-- Sankhani <strong> tags to <p> tags ndi kusintha malipiro a tsiku lomwe. -->
        <p>Thandizani kompani ya usafiri kuti muthandize ndi kuchititsa kuti mupange mchitidwe wa usafiri. Sintha ma data a kukhala ndi mavuto kapena zigwirizano zosayenera zomwe zimatha mu mchitidwe wa usafiri wa kompani.</p>
    </section>
    <br><br>

    <section>
        <h2>	Kulamulira ndi Malipiro</h2>

        <!-- Sankhani <strong> tags to <p> tags ndi kusintha malipiro a tsiku lomwe. -->
        <p>1. Kulamulira Mabukhu: Zofunikira m'malawo, ndi pafupi pa chilangizo cha galimoto.</p>
        <p>2. Galimoto ya Value Added Tax (VAT): Zofunikira m'malawo, ndi pafupi pa chilangizo cha galimoto.</p>
        <p>3. Othandizira Malipiro ndi Malipiro: Malipiro a chilungamo, tax ndi malipiro, n.k.</p>
    </section>
    <br><br>
</div>

<div class="text-container" v-if="selectedLanguage === 'Ch'">
    <h1>እንዴት ማሽን ለኢትዮጵያ በካሬ መግቢያ መንገድ ለመስጠት</h1>

    <section>
        <h2>የአስተዳደር ምክንያት</h2>

        <p>1. የማሽነሪ ጥቅል ምርመራ ምርመር: የተለያዩ መኪና እና የመረጃ ፈቃድ ያላገኙ መኪናዎችን ምርመር እና ውጭ የመረጃ ፈቃድ ማስጠበቅ ይችላሉ።</p>
        <p>2. መረጃ ማስታወሻ: ለማስተላለፍ የተራራውን ሥራ እና የታሪክ ፈቃድ ምርመራዎች ይቀላቀሉ።</p>
        <p>3. የተጓዳኝ መሸጎሪያ: የመረጃ ማስታወሻ የሚሆነውን መሸጎሪያ አቅርብ። መክፈያ ወደ ሚከተለው ምድቦች ይሄንን ማረጋገጥ ይችላሉ።</p>
        <p>4. ጉብዞ ማጠቃለያ: ማሽነሪ ጉብዞ ማጠቃለያን በመጠበቅ ይቀላቀሉ።</p>
    </section>
    <br><br>

    <section>
        <h2>የትምህርት ክስተት</h2>

        <!-- አዲስ የ<strong> ተለዋጭን ወደ <p> ተከታታይ ይሆናል እና ግንኙነቱን ውስጥ ይደሰቱ -->
        <p>1. የማሽነሪ ዋጋ: የማሽነሪ ዋጋ የአስተዳደር ክስተት።</p>
        <p>2. በሥራ ጊዜ ክስተት: አስተዳደር ማሽነሪ ሥራ እና ታሪክ ጊዜውን ይቀላቀሉ።</p>
        <p>3. የስራ ሂደት ዋጋ: የማሽነሪ ስራ ሂደት ዋጋ።</p>
        <p>4. ቁርስ እና ገበያ ዋጋ: ጉብዞ ቁርስ እና ገበያ የሚለው ዋጋ።</p>
    </section>
    <br><br>

    <section>
        <h2>መልእክቶች የሚጠቃለው የስልጠና ክስተት</h2>

        <!-- አዲስ የ<strong> ተለዋጭን ወደ <p> ተከታታይ ይሆናል እና ግንኙነቱን ውስጥ ይደሰቱ -->
        <p>ማሽነሪ ስልጠና ወይም ማስቀመጥ ስለሚችሉ የስልጠና ክስተትን ከሚፈልጉ ሥራ አቅርቦ መረጋገጥ ይችላሉ። የስልጠና እና አርማጅ ቀንቶችን ማረጋገጥ እንደሚችል ይረዳሉ።</p>
    </section>
    <br><br>

    <section>
        <h2>ቁርስ እና ገበያ</h2>

        <!-- አዲስ የ<strong> ተለዋጭን ወደ <p> ተከታታይ ይሆናል እና ግንኙነቱን ውስጥ ይደሰቱ -->
        <p>1. ጉብዞ ቁርስ: ለአንድ ሃገራት ቁርስ ነው። ይ        ቀረበን ለመስራት አልቀረብንም።</p>
        <p>2. ከዚህ በተጨማሪ በመክፈያ የሚሰጥ የጉብዞ ቁርስና ገበያ የሚለው ዋጋ።</p>
        <p>3. ሌሎች ቁጥሮች እና የሚለው የጉብዞ ቁርስ እና ገበያዎች። ከግንኙነት ታውቃላችሁ በኢትዮጵያ ማሽነሪ ሃገራት ስለመስጠት ይረዳሉ።</p>
    </section>
</div>

<div class="text-container" v-if="selectedLanguage === 'Co'">
    <h1>Kohecheko wa magari yaliyotumiwa kutoka Korea mwa Afrika</h1>

    <section>
        <h2>Maswala ya Kuingiza</h2>

        <p>1. Chagua na Nunua Gari Lililotumiwa: Chagua na nunua gari lililotumiwa kutoka kwa muuzaji wa kuaminika au jukwaa la mtandaoni.</p>
        <p>2. Andaa Nyaraka: Andaa nyaraka muhimu kama mkataba wa ununuzi, usajili wa gari, kibali cha kusafirisha, na nyaraka za usafirishaji.</p>
        <p>3. Andaa Usafirishaji: Fanya mkataba na kampuni ya usafirishaji na usafirishe gari hadi bandarini.</p>
        <p>4. Kibali cha Forodha: Kamilisha kibali cha forodha katika bandari ya marudio.</p>
    </section>
    <br><br>

    <section>
        <h2>Gharama Zinazohitajika</h2>

        <!-- Badilisha vitambulisho vya <strong> hadi <p> na alinganisha maandishi kushoto. -->
        <p>1. Bei ya Gari: Gharama ya gari lililonunuliwa.</p>
        <p>2. Ada ya Usafirishaji: Usafirishaji baharini na malipo ya bandari.</p>
        <p>3. Ada ya Bima: Gharama ya bima ya baharini.</p>
        <p>4. Kodi na Forodha: Kodi za forodha na kodi katika nchi ya marudio.</p>
    </section>
    <br><br>

    <section>
        <h2>Mipango ya Usafirishaji</h2>

        <!-- Badilisha vitambulisho vya <strong> hadi <p> na alinganisha maandishi kushoto. -->
        <p>Wasiliana na kampuni ya usafirishaji ili kuthibitisha na kuweka ratiba ya usafirishaji. Hakiki tarehe za kuondoka na kuwasili kulingana na ratiba ya kampuni ya usafirishaji.</p>
    </section>
    <br><br>

    <section>
        <h2>Kodi na Forodha</h2>

        <!-- Badilisha vitambulisho vya <strong> hadi <p> na alinganisha maandishi kushoto. -->
        <p>1. Kodi ya Forodha: Hubadilika kulingana na nchi, ni asilimia ya bei ya gari.</p>
        <p>2. Kodi ya Thamani ya Ongezeko (VAT): Hubadilika kulingana na nchi, ni asilimia ya bei ya gari.</p>
        <p>3. Kodi na Ada Nyingine: Inaweza kujumuisha ada za kibali cha forodha, kodi za mazingira, nk.</p>
    </section>
    <br><br>
</div>


<div class="text-container" v-if="selectedLanguage === 'Rw'">
    <h1>Uko gutanga imodoka z'abahinzi zituruka muri Koreya mu Afurika</h1>

    <section>
        <h2>Igikorwa cyo Kwiyandikisha</h2>

        <p>1. Hitamo no gutanga imodoka y'abahinzi: Hitamo n'ubanza imodoka y'abahinzi muri umuhinzi w'imyuga cyangwa platforme ya interneti.</p>
        <p>2. Kwiyandikisha ibyemezo: Kwiyandikisha ibyemezo byiza byerekana nk'ibyemezo bya gutanga, kwiyandikisha igari, permiti yo kuzabura, n'ibyemezo by'ubushobozi bw'usafirishya.</p>
        <p>3. Gufasha kuzasafisha: Gushyiraho umushyitsi w'usafirishya n'usafirisha igari kuri bandari.</p>
        <p>4. Kwamamaza ku kubura: Kamilisha kwamamaza ku kubura mu bandari ya cyerekezo.</p>
    </section>
    <br><br>

    <section>
        <h2>Amateraniro agomba</h2>

        <!-- Guhindura amazina <strong> kuri <p> ndetse kuganiriza ibyo byanditse hagati. -->
        <p>1. Igiciro cy'igari: Igiciro cy'igari wagiye gukorerwa.</p>
        <p>2. Ishyirwaho ry'usafirishya: Ishyirwaho ry'usafirishya mu majyambere n'ibimenyetso bya bandari.</p>
        <p>3. Ishyirwaho ry'ibyuma bishya: Igiciro cy'ibyuma bishya by'usafirishya.</p>
        <p>4. Imisoro n'ibiribwa: Imisoro yo kubura n'ibiribwa mu gihugu cy'icyo kiri mu buryo bw'icyerekezo.</p>
    </section>
    <br><br>

    <section>
        <h2>Itangazo ry'usafirishya</h2>

        <!-- Guhindura amazina <strong> kuri <p> ndetse kuganiriza ibyo byanditse hagati. -->
        <p>Siga umushyitsi w'usafirishya kugirango wibutse no guhagarika itangazo ry'usafirishya. Hagarika itariki y'ugushyingirwa no kugera kuri itariki y'ikiganza cy'usafirishya.</p>
    </section>
    <br><br>

    <section>
        <h2>Imisoro n'ibiribwa</h2>

        <!-- Guhindura amazina <strong> kuri <p> ndetse kuganiriza ibyo byanditse hagati. -->
