        <p>2. Ikimwemerezo cy'Inkunga y'Abacuruzi (VAT): Yavuye mu gihugu, ni icyumweru cy'igiciro cy'igari.</p>
        <p>3. Imisoro n'ibindi byahebuje: Biterwa n'imisoro yo kubura, imisoro yo gukumiriza amazi, nk.</p>
