<h3>Gahunda</h3>

Ndashaka kugerageza gushinga ikigo gikora imodoka gikora muri Afurika.
Ubu, igihe kirimo guhindura moteri zose zitwika imbere ziba imodoka z'amashanyarazi.
Bityo, imodoka nyinshi zikoze zitarakoreshwa, imodoka zasigaye zizakorwa mu gihugu no mu mahanga.
Ubanza, ndashaka kugerageza gusubizamo ubuzima izo modoka zakoreshejwe n'izidasubirwamo maze nkazohereza mu bihugu bitaratera imbere muri Afurika.
Igihugu cy’ubu gikennye cyane muri Afurika ntikishobora no gutunga imodoka ku muntu.
Nuko, ndatekereza ko ari ngombwa kongera ubukungu bw'icyo gihugu. Gutanga imodoka bikora ubucuruzi,
kandi ubucuruzi bushobora kugeraho umusaruro w'ubukungu.
Mu yandi magambo, gutanga imodoka zihagije muri Afurika no kuzohereza bizaha ubukungu bwa Afurika umwuka mushya.
Niteze ko ubukungu bw'ibyo bihugu buzaba hasi cyane ku buryo batazashobora kugura ikindi kintu uretse kugenda n’amaguru.
Uhereye icyo gihe, hazaba urugamba rw’imodoka. Ndakeka ko bizabaho.
Kugera ubwo, tugomba gutanga serivisi z'ubudahemuka z'ubudahemuka no gutunganya imodoka zacu twohereza.
Amaherezo, abana ba Afurika bazarya neza kandi banywe amazi meza.
Ahubwo yo kugurisha imodoka, tuzashyira imbere serivisi z'ubudahemuka no kongera imodoka.
Mu yandi magambo, imiterere y'ikigo ikora ibishoboka byose mu buryo bw'imikino y'umutwe w'ubuhanga mu buryo bw'ibikorwa.
Ibyo ntekereza ko ari igikorwa cy'urukundo. Urubuga rwo kugurisha imodoka y'isekuru ry'igikundiro ni na sisitemu (sisitemu) y'urukundo.
Bivuze ko byinshi bizashyirwa mu guhanga byinshi. Ndetse, bizongera ibiciro.
Ariko, urubuga rwo kugurisha imodoka y'igitsina kizakora kugirango ikomeze inyungu kandi ikomeze icyizere imbere y'abashoramari ba mbere.
Sisitemu y'amafaranga n'ibigo biza binyuze muri urukundo rwo guhanga imirimo n'imirimo muri Afurika izamenyekana kugeza igihe cyose cyuzuye urukundo.
<br> <br>

<h3>Ikigo cy'imibare</h3>

Ikigo cya SmileNumber Motors giteganya gukora laboratoire yo gukora ubushakashatsi ku mibare.
Ubanza, nteganya gutegura itsinda ry'ubushakashatsi ry'abaganga mu kiciro cya postdoctorat bamaze guhabwa dogitora mu mibare.
Kandi nteganya gutegura ibiganiro byinshi byishimye n'abaprofeseri b'imibare bamaze gukura.
Ubanza, nteganya gukodesha uruganda ruto rwahagaritswe cyangwa ikibuga maze tukahavugurura.
Mu yandi magambo, ni ikigo cy'ubushakashatsi bw'imibare cy'ubwoko bw'ikibuga.
Nyuma y'uko ikigo kimaze gushyirwaho, nteganya gushinga ikigo cy'ubushakashatsi bw'imibare, SmileNumber Motors,
hamwe n'ikibuga kinini nk'icyumba cy'indege n'ububiko bunini bw'ibitabo by'imibare.
Ubu, bizatanga cafe yo kwiga n'ahantu hameze nk'ahantu ho kwiga hamwe n'igice cyo kubika imodoka,
kugaragaza no kubungabunga. Ahantu hafunguye hazatanga ibidukikije aho dushobora kwiga imibare mu buryo bwimbitse kandi bishimishije hamwe n'ikibaho kinini gishyizweho.
<br> <br>

<h3>Imiterere ya SmileNumber Motors(Sisitemu)</h3>

SmileNumber Motors izakoresha imiterere y'igihe cyo gusubiza inyuma mu mukino w'umutwe w'ubuhanga.
Kandi izuzura urukundo. Nta kuntu ikigo gikora imodoka gishobora gukorwa muri Afurika no mu bihugu bikiri mu nzira y'amajyambere,
ubukungu ni buto cyane. Mu yandi magambo, n'iyo waba ushaka kugura byinshi,
ibyifuzo by'ubukungu byo kubigura ntibibasha kugerwaho. Igisubizo kuri ibi ni ugutanga akazi n'akazi gashobora gukorwa mu buryo bw'aho igihe cyose ugurishije imodoka.
Nuko, iyo ugurishije imodoka, ugomba no gutanga akazi k'aho n'akazi. Mu yandi magambo, imiterere y'ikigo ikora ingaruka z'igishoro kizaboneka mu gihe kizaza.
Nuko rero, ni uburyo bwo gukoresha uburyo bw'igihe bwo gusubiza inyuma bw'umukino w'umutwe w'ubuhanga bwa mbere.
Nuko rero ntekereza ko ari urukundo iyo uhanga imirimo n'imirimo ikora ku bagura imodoka.
Nta kuntu ikigo gikora imodoka cyashora imari muri Afurika yose kandi kigakora imodoka 20-30,
ntikizashobora gukomeza ikigo keretse habonetse amafaranga.
Uzahanga ingaruka z'igishoro n'umusaruro w'igihe kizaza.
Nuko rero, ibi ni uburyo bw'ikigo busobanura ubukungu n'ingaruka z'igihe mu buryo bwo gusubiza inyuma ibyabaye.
(Mu mibare, isezerano ry'ibyago ritazabaho mu gihe cyo gusubiza inyuma mu gihe cy'umusaruro w'ikigo.
Mu yandi magambo, irasezeranya ibyago mu gihe cyo gusubiza inyuma kandi ikabisobanura mu gihe cy'igihe.)
Mu yandi magambo, niba hatari urukundo nk'uko igihe kigenda, imiterere y'ikigo ntiyasobanurwa.
<br> <br>

Mu gusobanukirwa uburyo bwo gusubiza inyuma bwo gusubiza inyuma bw'umukino w'umutwe w'ubuhanga,
tugomba kuba tuzi ikintu.
Mu buryo bwo gusubiza inyuma algorithm intuitif y'umukino w'umutwe w'ubuhanga,
imiterere yacu y'ubwonko n'imiryango isobanura ibyago bitabaho mu gihe cyo gusubiza inyuma.
Nuko rero, urugero ruri hejuru rusobanura urukundo mu gihe cy'igihe mu gihe nyacyo.
Nuko rero, mu gihe cy'igihe, niba hatari urukundo, imiterere y'ikigo ntiyasobanura umusaruro w'ubukungu cyangwa ingaruka.

