    <div class="text-container" v-else-if="selectedLanguage === 'Rw'">
     
     <h1>"Mwigisha w'ibihe byose ni SmileNumber Motors"<br>
      <br>
     "SEKA! SEKA! SEKA!"</h1>
     <br>

     <p>
    <h3>Intego</h3>

    Ubuyobozi buhebuje, abakozi baseka, abakiriya banini baseka: icyerekezo cya SmileNumber Motors ni "gukomeza gucunga umunezero wawe."
    (i) Imibare yacu y’abaseka izakomeza umunezero wawe binyuze mu micungire idahoraho y’abakiriya.
    (ii) Umunezero utangirira ku Ishyirahamwe ryacu ry’Imibare ishimishije ukagera kuri wowe.
    (iii) Umunezero ukorwa na SmileNumber Motors uzakugeraho mu buryo butandukanye bw’ibicuruzwa, serivisi, n'ibikoresho.
    (iv) Binyuze mu mushinga wa Afurika ishimishije, uzahora useka.
    Intego yacu ya nyuma muri SmileNumber Motors ni ukuzana ibyishimo byacu, inseko, n’umunezero kuri wowe.
    Byoherezwa kuri wowe, kandi iyo umukiriya yongeye guseka, habaho uburyo bwo guhanahana hagati yabo.
    Duhanga kandi tukarema ibitekerezo nk'ibi. Intego yacu y'ibanze y'ubucuruzi ni ugushimisha abakiriya.
    Biragaragara ko abakiriya bashimishwa cyane iyo baseka ku bitekerezo na serivisi dutanga.
    Ushobora buri gihe kubona inseko zacu ahantu hose ku isi ku giciro cyangwa serivisi ihwanye nayo.
    Ishyirahamwe ry’Imibare ishimishije rishinzwe gucunga umunezero wawe n’umunezero. Hano hari icyitegererezo cy’iminsi mikuru myinshi y’umunezero, inama, n’ibirori bigomba gukorwa.
    SmileNumber Motors zacu zizagira ijambo ku isi nyuma ya Microsoft. <br> <br>

    <h3>Gahunda</h3>

    Ndashaka kugerageza gushinga uruganda rw'imodoka rukorera muri Afurika. 
    Ubu, ibihe birahindura moteri zose zisanzwe ziba imodoka z'amashanyarazi.
    Ku buryo, imodoka nyinshi zikoreshwa, imodoka zasenyutse zizakorwa mu gihugu no mu mahanga. 
    Icya mbere, ndashaka kugerageza kuzahura izo modoka zakoreshwaga n’izindi modoka zasenyutse no kuzohereza mu bihugu bikennye muri Afurika. 
    Igihugu gikennye kurusha ibindi muri Afurika ntikibasha no kugira imodoka y’umuntu. 
    Ku buryo, ntekereza ko ari ngombwa kongera ubukungu bw'icyo gihugu. Gutanga imodoka byongera gutanga, 
    kandi gutanga hatambaye kw'ubukungu.
    Mu yandi magambo, gutanga imodoka zihagije muri Afurika no kuzohereza bizazana ubuzima bushya mu bukungu bwa Afurika. 
    Ndatekereza ko igipimo cy’ubukungu bw’ibyo bihugu kizaba kiri hasi cyane ku buryo batazashobora kugura ikintu icyo ari cyo cyose uretse kugenda.
     Kuva icyo gihe, hazabaho gushaka cyane imodoka. Ntekereza ko bizaba. 
     Kugeza ubwo, tugomba gutanga serivisi z’inyuma z’umutima n’amahirwe yo gusana imodoka twohereza. 
     Mu mpera, abana b'Afurika bazarya neza banywe amazi meza. 
     Ahubwo y'uko kugurisha imodoka, tuzashyira umwihariko kuri serivisi z'inyuma z'ubunyangamugayo no gusubiramo imodoka. 
     Mu yandi magambo, imiterere y'ikigo ikora ibyasohoka mu buryo bw'algoriti ya puzzle mu buryo bw'ibinyuranye. 
     Nicyo ntekereza nk'igikorwa cy'urukundo. Uburyo bwo kugurisha hanze imodoka iseka nayo ni sisitemu y'urukundo. 
     Bisobanura ko hazashorwa imbaraga nyinshi mu gukora byinshi. Byongeye kandi, bizazana ibiciro byiyongera. 
     Ariko, urubuga rwo kugurisha hanze rwa SmileNumber Motors ruzakora mu buryo bwo kubaka uburambe no kugira icyizere imbere y’abashoramari ba mbere. 
     Uburyo bw’amafaranga n’amakampani anyura muri uru rukundo kugirango akore akazi n’imirimo muri Afurika buzashyirwa mu rwego rw’ubumwe kugeza igihe cyose kizamara cyuzuye urukundo. 
     <br> <br>

    <h3>Ikigo cy’Imibare</h3>

    Ikigo cya SmileNumber Motors giteganya gukora ikigo cy’ubushakashatsi bw’imibare. 
    Icya mbere, ndateganya gushyiraho itsinda ry’ubushakashatsi ry’abaganga bari mu kiciro cy’icyiciro cya nyuma cy’icyiciro cya kaminuza bamaze kubona dogiteri mu mibare. 
    Kandi ndateganya gutegura amasomo menshi y’ibyishimo hamwe n’abarimu ba matematike bageze mu kiruhuko. 
    Icya mbere, ndateganya gukodesha uruganda ruto rwatawe cyangwa ububiko no kuvugurura ahongaho. 
    Mu yandi magambo, ni ikigo cy’ubushakashatsi bw’imibare yo mu bubiko. 
    Nyuma y'uko ikigo kimaze guhagarara, ndateganya gushyiraho ikigo cy’ubushakashatsi bw’imibare, SmileNumber Motors, 
    hamwe n'ububiko bunini nka hangar y’indege n’igitabo kinini cy’ibitabo bya matematike. 
    Ubu, kizatanga cafe y'ubusomyi n’ahantu hameze nko mu isomero hamwe n’akabari k'ahantu imodoka zishobora kubikwa, 
    kwerekanwa, no kubungabungwa. Ahantu hafunguye hazatanga ibidukikije aho dushobora kwiga imibare mu buryo bukomeye kandi bushimishije hamwe n'urukuta runini rw'umukara rwashyizweho. 
    <br> <br>

    <h3>Imiterere ya SmileNumber Motors(Sisitemu)</h3>

    SmileNumber Motors izashyiramo uburyo bw’imiterere bw’umwanya wo gusubira inyuma ku mukino wa puzzle. 
    Kandi izuzura urukundo. Ntacyo bimaze uko uruganda rw’imodoka rukorewe muri Afurika no mu bihugu bikiri mu nzira y’amajyambere, 
    igipimo cy’ubukungu ni gito cyane. Mu yandi magambo, ntacyo bimaze uko ushaka kugura, 
    uburyo bwo kugura ntabwo bushobora kugerwaho. Igisubizo cy’ibi ni ugutanga akazi n'akazi gashobora gukorwa mu gihugu hose igihe cyose ugurishije imodoka. 
    Kubw’ibyo, iyo ugurishije imodoka, ugomba no gutanga imirimo n'imirimo yo mu gihugu. Mu yandi magambo, imiterere y'ikigo ikora ingaruka z'imitungo y'imitungo izakorwa mu gihe kizaza. 
    Kubw’ibyo, ni uburyo bwo gushyiramo uburyo bw'algoriti bw'umwanya wo gusubira inyuma ku mukino wa puzzle. 
    Ku buryo ntekereza ko ari urukundo iyo ukora akazi n’imirimo ishobora gukorwa n'abagura imodoka. 
    Ntacyo bimaze uko uruganda rw’imodoka rushora muri Afurika hose rugakora 20-30, 
    ntiruzashobora gusigasira uruganda igihe nta mafaranga ahari. 
    Uzashyiraho ingaruka z'imitungo n'imitungo y’igihe kizaza. 
    Kubw’ibyo, iyi ni imiterere y'ikigo isobanura igipimo cy’ubukungu n'ingaruka z'igihe mu buryo bw’ibinyuranye n’ibyabaye. 
    (Mu mibare, isezeranya igikorwa gishoboka kitazaba mu buryo bw'igihe mu gihe cy’ikorwa ry'ikigo. 
    Mu yandi magambo, isezeranya igikorwa gishoboka mu buryo bw'igihe n'icyo isobanura mu gihe cy'igihe.) 
    Mu yandi magambo, niba nta rukundo nka uko igihe gihita, imiterere y'ikigo ntabwo isobanurwa. <br> <br>

    Mu gihe cyo gusobanukirwa algoriti y'umwanya wo gusubira inyuma ku mukino wa puzzle, 
    tugomba kuba dufite ubumenyi bw’ikintu. 
    Mu buryo bw’ibinyuranye n'uburyo bw'algoriti y'ibintu by'ibintu byo gusubira inyuma, 
    imiterere yacu y'umwimerere n'imibereho isobanura uburyo butabaho mu buryo bw’ibinyuranye n'igihe. 
    Kubw’ibyo, icyitegererezo cyavuzwe haruguru gisobanura urukundo mu gihe cy'igihe mu gihe nyacyo. 
    Kubw’ibyo, mu gihe cy'igihe, niba nta rukundo, imiterere y'ikigo ntisobanura uburyo ubwo aribwo bwose bw’ubukungu cyangwa ingaruka.

    </p>
