    plan: '계획: 아프리카에서 운행될 자동차 회사를 아프리카 현지에 설립해 보고 싶습니다. 현재 시대는 전반적으로 내연기관 자동차에서 모두 전기차로 바뀌고 있는 시대 입니다. 그러므로 많은 중고차량, 폐차량이 국내, 국외에서 만들어 질 것입니다. 우선은 그러한 중고차, 폐차량을 다시 부활하여 아프리카나 개발도상국가에 수출하는 업무부터 시작해 보려고 합니다. 현재의 아프리카의 최빈국은 자동차 한대를 개인이 소유하기도 어려운 환경이라고 알고있습니다. 그러므로 우선은 그 국가의 경제규모를 키워야 한다고 생각합니다. 자동차의 공급은 유통을 만들고, 유통은 결국 경제적 흐름을 만들어 낼 것이라고 예상합니다. 즉, 다시말해서 우선 아프리카에 충분한 자동차를 공급하고 수출하는 것 만으로 아프리카 경제에 활력을 불어 넣을수 있을 것이라고 생각합니다. ',
    mathLab: '수학 연구소: 웃는숫자자동차 회사 안에는 수학 연구소를 운영할 예정입니다. 처음 몇분 정도의 수학박사학위를 받으신 분들을 모셔서 박사후 연구원 과정의 연구 소를 계획중 입니다. 그리고 은퇴하신 수학 교수님들을 모셔서 여러 즐거운 강연을 기획중 입니다. 우선은 작은 폐공장이나 창고를 빌려서 리모델링하여 그 장소를 제공할 예정입니다. 즉, 창고형 수학연구소 입니다. 나중 회사가 안정화된 후에는 비행기 격납고 만큼 큰 창고에 수학도서관과 함께 웃는숫자 수학연구소를 설립할 예정입니다. 현재는 자동차를 보관, 진열, 정비 할수 있는 장소의 한켠을 스터디카페와 같이 만들어서 수학연구 장소로 제공할 것 입니다. 그러한 열린공간에는 커다란 칠판을 설치하여서 서로 웃으면서 진지하고 즐겁게 수학연구를 할수 있는 환경을 제공할 것입니다.',
    system: '스마일넘버 모터스 시스템: 웃는숫자자동차에는 슬라이딩 퍼즐게임의 역시간 구조를 적용할 것 입니다. 그리고 그것을 사랑으로 가득 채울 것 입니다. 아프리카와 개발 도상국은 아무리 자동차 회사를 만든다고 해도 경제의 크기가 너무 작습니다. 즉, 구입하고 싶어도 구입할 수 있는 경제적 여건이 되지 않습니다. 그것의 해결 방법으로 자동차 한대를 판매할 때마다 그것의 경제학 구조 즉, 직업과 일자리를 함께 만들어서 제공할 예정입니다. 그러므로 자동차를 파는 것과 동시에 현지에서 할수 있는 일자리 그리고 직업을 제공해야 합니다. 그렇게해야 자동차 회사 또한 유지될수 있습니다. 이러한 구조는 미래에서 그 자금의 흐름이 흘러 들어오는 효과를 만듭니다. 즉, 자금은 미래에 만들어지는 것입니다. 그러므로 첫번째 슬라이딩퍼즐게임의 구체적인 역시간 흐름 알고리듬 적용의 방법입니다. 따라서 자동차를 구입하는 분들을 위하여 일할수 있는 여건 그리고 직업도 만드는 것이 사랑이라고 생각합니다. 아무리 자동차회사를 아프리카 전역에 투자를 받아서 20-30개를 만든다고 해도 자금이 없으면 자동차를 구입할 수 없고 결국 회사도 유지되지 못할 것 입니다. 앞과같이 슬라이딩 퍼즐게임의 알고리듬을 적용하여 경제학적 자금 그리고 흐름을 역 시간대로도 흐르게 만들 것 입니다.',
    usedCarExport: '웃는숫자자동차의 중고차 해외 수출 플랫폼은 주 품목으로 화물자동차나 버스, 승합차를 수출할 것입니다.(물론 보통의 일반 승용차도 수출할 것입니다.) 그런데 문제는 아프리카의 험난한 지형에 맞도록 개조를 해야하고 그것을 현지에서 제공할 수 있어야 합니다. 그것을 구현하는 단계가 쉽지는 않을 듯 합니다. 어느정도 버스 그리고 화물차, 승합차가 포화상태에 이르면 모두가 자동차 사용의 편리함을 알게되고(현재는 걸어다니는 것만 못하다고 합니다.) 그때부터 자동차 골드러쉬가 일어날 것 이라고 예상합니다. 그때까지 수출한 자동차의 애프터 서비스와 개조옵션을 성실하게 제공해야 합니다. 결국 아프리카 아이들은 배부르게 먹고, 깨끗한 물을 마시게 될 것 입니다. 자동차의 판매보다 판매후 애프터 서비스 그리고 차량의 개조에 더욱 그 비중을 크게 할 것 입니다. 즉, 다시말해서 그러한 구조는 회사에 슬라이딩퍼즐의 역시간 알고리듬을 적용한 결과를 만들어 냅니다. 그것이 무조건 이익을 위해서 판매하는 것보다 진정 아프리카를 사랑하는 행위라고 생각합니다. 앞과같이 웃는숫자 자동차 해외 판매 플랫폼에도 또한 사랑의 역시간 구조(system)를 적용할 것 입니다. 비중을 크게 한다는 것은 더욱 많은 노력이 들어갈 것을 의미합니다. 그리고 또한 꽤 큰 추가 비용도 발생 할 것입니다. 그러나 웃는숫자자동차 해외판매 플랫폼은 더욱 많은 돈을 벌기 위하여 운영하려고 하는 것이 아닙니다. 자동차회사 경험쌓기 그리고 앞의로의 투자자들에게 신뢰를 만들기 위해서 입니다. 이러한 역시간으로 흐르는 자금과 회사의 구조는 아프리카에 직업 그리고 일자리를 만들면서 일반화 시킬 예정입니다. 즉, 모든 시간대가 온전히 사랑으로 가득차는 그날까지 웃는숫자자동차는 달려 갑니다.',
    reverseTimeFlow: '우리는 위와 같이 슬라이딩퍼즐게임의 역시간 흐름 알고리듬을 이해할때 주의해야 할 사항이 있다. 슬라이딩퍼즐게임의 역시간 흐름 비직관 알고리듬은 우리의 자연 그리고 사회구조가 역시간으로도 발생하지 않는 확률을 정의하고 있다고 전한다. 그러므로 위의 예제는 마치 역시간 흐름으로 정시간 흐름대에서 사랑을 정의하고 있다. 따라서 역시간 대의 시간 흐름에서는 사랑이 없으면 어떠한 경제적 흐름, 효과도 발생하지 않도록 회사의 구조를 정의한다.(수학적으로 회사에서 발생하지 않을 확률적 사건을 역시간 흐름대에서 정의하는 것이다. 즉, 다시말해서 정시간 대에서 역시간 방향으로 발생하는 확률적 사건을 약속하므로 동시에 역시간 방향의 흐름에서 발생하지 않는 확률적 사건이 정의된다.) 즉, 성경의 구절과 같이 사랑이 없으면 아무것도 아니다.',
  
